import React,{Component} from 'react';


class Profile extends Component
{
    constructor(props)
    {
        super(props);
        this.state = 
        {
            user:null
        }
        this.fetchuserdata();
        
    }

   

    fetchuserdata()
    {
        var url = this.props.url+'/uprofile/'+this.props.userprof;
        fetch(url,{
            method:'GET',
            headers:{
                Authorization:'Token '+this.props.token
            }
        }).then((response)=> response.json()
        .then((data) => {
            console.log(data);
            this.setState({
                user:data
            })
        }))
    }

    render()
    {
        if(this.state.user)
        {
            if(this.state.user.username!=this.props.userprof)
            this.fetchuserdata();
        return(
            <div className='container'>
               <div className='profilewrapper'>
                <img className='profileimage' src={this.props.url+this.state.user.profileinfo.profile_pic} />
                <br/>
                <h4 className='profile-username'>{this.state.user.username}</h4>

                </div>
            </div>
        )
        }
        else
        return(
            <div>

            </div>
        )
    }

}

export default Profile;
