import React, {Component} from 'react';
import { Link } from 'react-router-dom';


class AddPost extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            posted:false
        }

        this.handleaddpost = this.handleaddpost.bind(this);
    }

    handleaddpost(e)
    {
        e.preventDefault();
        console.log('Added');
        console.log(this.props.url)
        var addposturl = this.props.url+'/post';
        const form = new FormData();
        // const addpost_form = new FormDAta();

        form.append('content',e.target.caption.value)
        form.append('location',e.target.loc.value)
        form.append('image',e.target.image.files[0])
        var data = {
            'content':e.target.caption.value,
            'location':e.target.loc.value,
            'image':e.target.image.files[0]
        }

        fetch(addposturl,{
            method:'POST',
            headers:{
                Authorization:'Token '+this.props.token
            }, 
            body:form
        }).then((response)=>{
            console.log(response);
            this.setState({
                posted:true
            })
            
        })
        
       
        
    }



    render()
    {
        return(
            
            <div className='container addpost-wrapper'>
                {!this.state.posted?<div>
                <h3>Add Post</h3>
                <hr />
                <div>
                    <form id='form1' method="POST" enctype="multipart/form-data" onSubmit={this.handleaddpost}>
                    <textarea  name="caption" id='caption' className='postcaption' placeholder="What's on your mind?..." required/> <br/>
                    <input type="file" name="image" accept='image/*' id='image' /> <br /> <br/>
                    <input type='text' name='loc' className='postloc' placeholder='Add Location' /><br /> 
        
        <input type="submit" className='btn btn-primary addpost-submit' value='Post' /> 
                   
                    
                    </form>
                </div> </div>:<div> </div>}
            </div>
        )
    }
}


export default AddPost;