import React, {Component} from 'react';
import Post from './PostComponent';
import './Home.css';

class Home extends Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            fetchdata:false
        }
      
        this.fetchposts();


    }

    fetchposts()
    {
       const posturl = this.props.url+'/post';
       
       console.log(posturl)
       fetch(posturl,{
           method:'GET',
           headers:{
               Authorization:'Token '+this.props.token
           }
       }).then((response)=> response.json())
       .then((data)=>{
           console.log(data);
           this.setState ({
               posts:data,
               fetchdata:true
           })
        //    console.log(this.state.posts[0].location)
       })
    }

    renderview()
    {
        if(this.state.fetchdata)
        {
            const postview = this.state.posts.map((post) => {
                return(
                <div>
                    <Post url={this.props.url} post={post} token={this.props.url} />
                </div>
                )
            })
            return(
                <div className='posts'>
                    {postview}

                </div>
            )
        }
        else
        {
            return(
                <div>

                </div>
            )
        }
    }

    

    render()
    {
       

        return(
            <div className='container'>
                <div id='home'>
               <h3>Home</h3> 
               <hr/>
               
       <div className='posts-wrapper'>
        {this.renderview()}
        
        </div>
       
        </div>
       
            </div>
        )
    }
}

export default Home;