import React,{ Component } from 'react';
import { Link } from 'react-router-dom';


class Login extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            token:''
        };
        this.handleForm = this.handleForm.bind(this);
    }

    handleForm(e)
    {
        e.preventDefault();

        var loginurl = this.props.url+'/api-token-auth/';
        var data = {
         username : e.target.username.value,
         password : e.target.password.value
        }
        fetch(loginurl, {
            method:'POST',
            headers:{
              'Content-type': 'application/json',
              
            },
            body:JSON.stringify(data)
          }).catch(function(error){
            console.log('Error ')
          }).then((response)=> response.json()
          ).then((data) => {
            if(data.token!=null)
            {
            this.setState({
              token:data.token,
            })
            localStorage.setItem('login',true)
            localStorage.setItem('token',this.state.token)
            this.setState({
              error:''
            })
            this.props.handlelog(this.state)
            } 
            else
            {
              console.log(data)
              this.setState({
                error:'*Invalid Credentials'
              })
            }
          }
           
          ).then(()=>{
          
          console.log(this.state.token)
          
         
          })
          localStorage.setItem('user',e.target.username.value) 
    }
   

    render()
    {

        return(
            <div className='container'>
             
               <h3 className='login-title' >Login</h3>
               <div className='signup-btn-w'>
               <Link className='side-link'  to="/signup">
                 SignUp 
                <i class="fa fa-user-plus signuplink" aria-hidden="true"></i>
              
               </Link>
               </div>
               
                <div className = 'login-wrapper'>
                
                    <div className='logbox'>
                    <span className='logerror'>{this.state.error}</span><br/>
                        <form onSubmit ={this.handleForm} >
                        <input type='text' name='username' className='inp' placeholder='username' />
                        <br />
                        <input type='password' name='password' className='inp' placeholder='password'/>
                        <br />
                       
                        <button type='submit' className='btn btn-primary loginbtn btn-large' >LogIn </button>
                        </form>
                    </div>
                    
                </div>

                {/* <button onClick = {()=>this.props.handlelog(this.state)} > Button </button> */}
            </div>
        )
    }
}

export default Login