import React,{ Component, useEffect, useState } from 'react';
import './App.css';

import { BrowserRouter } from 'react-router-dom';
import Main from './components/MainComponent';
import Login from './components/LoginComponent';
import { Routes, Route, useLocation, Link} from 'react-router-dom';
import Navbar from './components/NavBar';
import SignUp from './components/SignUpComponent';

class App1 extends Component
{

  constructor(props)
  {
    super(props);
    const login = localStorage.getItem('login')=='true'
        // user:localStorage.getItem('user'),
    const token = localStorage.getItem('token')
    
    this.state =
      {
       
        mystyle : {
          'color':'red'
        },
        url:'http://localhost:8000',
        login:localStorage.getItem('login')=='true',
        // user:localStorage.getItem('user'),
    token:localStorage.getItem('token'),
      }
      this.logout = this.logout.bind(this);
      
    
  }
  componentDidMount()
  {
    this.userdataupdate(this.state.login,this.state.token)
    
  }

 userdataupdate(login,token)
 {
   console.log(token)
   console.log('Userdata')
   console.log(this.state.url)
  
   const userdataurl = this.state.url+'/userprofile';
   if(this.state.login==true)
   fetch(userdataurl,{
    method:'GET',
    headers:{
        Authorization:'Token '+ token
    }
}).then((response)=> response.json())
   .then((data) =>
   {
     console.log(data)
     if(data.detail!='Invalid token.')
     this.setState({
       user:data
     })
     else
     {
       this.logout();
     }
   })

 }

 


  handlelogin(data)
  {
    var token = data.token;
    console.log(token);
    this.setState({
      login:true,
      token:data.token,
    })
    this.userdataupdate(true,token);
  }

  logout()
  {
    localStorage.setItem('login',false)
  localStorage.setItem('user',null)
  localStorage.setItem('token',null)
  this.setState({
    login:false,
    token:null,
    user:null,
  })
  
  }

 


  render()
  { 
    const Renderview = () =>
  {
   
    if(this.state.login=='true' || this.state.login==true )
    {
      return(
        <div>
        
          <Main user = {this.state.user} token =  {this.state.token} url = {this.state.url}   />
       </div>
      )

    }
    else
    {
      return(
        <div>
           <BrowserRouter>
           <Routes>
           <Route  path="/login" component={() => <Login handlelog = {(data) => this.handlelogin(data)}  url = {this.state.url} />} />
           <Route  path="/signup" component={() => <SignUp url = {this.state.url} />} />
        {/* <Redirect to='/login' /> */}
        </Routes>
        </BrowserRouter>
       
   
    </div>
      )
    }
  }
 
  
    return(
      <div>
     <Navbar login={this.state.login} url={this.state.url} logout={()=> this.logout()} user={this.state.user} />
      
     <Renderview />
     
     </div>
    )
  }
}

function ABC()
{
  
  const queryParams = new URLSearchParams(window.location.search)
  const term = queryParams.get("q")
  const [query, setQuery] = useState(term)
  const [vid_data, setData] = useState([])
  useEffect(() => {
    // setQuery(term);
    console.log(term)
    const url = `https://youtube.googleapis.com/youtube/v3/search?part=snippet&q=${term}&key=AIzaSyDggO-UNEQMNpdinDixaawokU68Lc4Ca5M&maxResults=20&type=video`
    fetch(url).then(resp => resp.json()
    ).then(data => {
      console.log(data)
      setData(data['items'])
    })
  },[new URLSearchParams(window.location.search).get('q')])
  console.log('Search')
  const location = useLocation()

  // console.log(location)
  return(
    <div className='container container2'>
        {query?<h2>Search Results for "{query}"</h2>:<div/>}
        <br />
      {vid_data.map(vid => (
                <div>
                <h5>{vid['snippet']['title']}</h5>
                <img width="200" src={vid['snippet']['thumbnails']['medium']['url']} />
                <br />
                <Link to={`/video?url=https://www.youtube.com/watch?v=${vid['id']['videoId']}`}> Link
                </Link>
                <hr />

                </div>
            ))}
    </div>
  )
}


function VideoComp(props)
{
  const queryParams = new URLSearchParams(window.location.search)
  const term = queryParams.get("url")
  const [url, setUrl] = useState(term)
  const [vid_data, setVideo] = useState();

  useEffect(() => {
    // setUrl()
    const api_end = `${props.host_url}/api/video/?url=${term}`;
    fetch(api_end).then(resp => resp.json()
    ).then(data => {
      console.log(data)
      setVideo(data)
    })

}, [])

  return (

    <div className='container container2'>
      {vid_data?<div>
        <h4>{vid_data['title']}</h4>
        <video width="320" controls>
  <source src={vid_data['url']} type="video/mp4" />
  <source src="movie.ogg" type="video/ogg" />
Your browser does not support the video tag.
     </video> 
     <br />
     <br />
     <h6>Audio:</h6>
     <audio controls>
  <source src={vid_data['audio']} type="audio/ogg" />
  <source src="horse.mp3" type="audio/mpeg" />
Your browser does not support the audio element.
</audio>
      </div>:<div></div>}
      </div>

  )
}


function App()
{

  const host_url = 'https://ytzdownloader.herokuapp.com'
  return(
   
      
      <BrowserRouter>
      <div>
      <Navbar />
      {/* <h1>Youtube Downloader</h1> */}
      <Routes>
        {/* <Route path='/' element={<Navbar />}> */}
        <Route  path='/search' element={<ABC />}> </Route>
        <Route path='/video' element={<VideoComp host_url={host_url} />} />
        {/* </Route> */}
      </Routes>

      </div>
      
      </BrowserRouter>
    
  )
}



export default App;
