import React,{Component} from 'react';
import { Link } from 'react-router-dom';


class SignUp extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            'errors':[],
            'success':false
        }
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e)
    {
        e.preventDefault();
        console.log('Submitted');
        var signupurl = this.props.url + '/register';
        if(e.target.password1.value!=e.target.password2.value || e.target.password1.value.length<8)
        {
            this.setState({
                'errors':['Passwords doesnot match or length is less than 8']
            })
        }
        else
        {
            this.setState({
                'errors':[]
            })
            var data = {
                first_name : e.target.firstname.value,
                last_name : e.target.lastname.value,
                username : e.target.username.value,
                email : e.target.email.value,
                password : e.target.password1.value
            }
            fetch(signupurl, {
                method:'POST',
                headers:{
                  'Content-type': 'application/json',
                  
                },
                body:JSON.stringify(data)
              }).catch(function(error){
                console.log('Error ')
              }).then((response) => response.json())
              .then((data) => {
                  if(data.errors)
                  {
                      this.setState({
                          'errors':data.errors
                      })
                  }
                  else if(data.success)
                  {
                      this.setState({
                          'success':true
                      })

                  }
              })

        }
        
    }

    render()
    {
        
        return(
            <div className='container'>
                <div className='signup-wrapper'>
                <h1>SignUp</h1>
               
                <br></br>
               
                {this.state.errors.map((error) => {
                    return(
                        <div>
                            <p className='errors'>*{error}</p>
                            </div>
                    )
                })}
                {this.state.success?<div>
                    <h3>Your account has been successfully created</h3>
                    <br></br><br></br>
                    <Link className='side-link'  to="/login">
                 Login 
                <i class="fa fa-user-plus signuplink" aria-hidden="true"></i>
                </Link>
                </div>:
                 <form onSubmit={this.handleSubmit}>
  <div className="form-row">
    <div className="form-group col-md-6">
      <label for="inputEmail4">First Name</label>
      <input type="text" name='firstname' className="form-control" id="inputEmail4" required />
    </div>
    <div className="form-group col-md-6">
      <label for="inputPassword4">Last Name</label>
      <input type="text" name='lastname' className="form-control" id="inputPassword4" required />
    </div>
  </div>
  <div className="form-group">
    <label for="inputAddress">Username</label>
    <input type="text" name='username' className="form-control" id="inputAddress" required />
  </div>
  <div className="form-group">
    <label for="inputAddress2">Email Address</label>
    <input type="email" className="form-control" id="inputAddress2" name='email' required />
  </div>
  <div className="form-row">
    <div className="form-group col-md-6">
      <label for="inputEmail4">Password</label>
      <input type="password" name='password1' className="form-control"  required />
    </div>
    <div className="form-group col-md-6">
      <label for="inputPassword4">Confirm Password</label>
      <input type="password" name='password2' className="form-control"  required />
    </div>
  </div>
  
  <button type="submit" className="btn btn-primary">Sign Up</button>
  <br></br><br></br>
  <div className='login-text'>
  Already have an account? 
   <Link className='side-link'  to="/login">
                 Login
                <i class="fa fa-user-plus signuplink" aria-hidden="true"></i>
              
               </Link>
               </div>
</form>}
                </div>
            </div>
        )
    }
}

export default SignUp;