import React,{Component} from 'react';
import { Link } from 'react-router-dom';


class Post extends Component
{
    constructor(props)
    {
        super(props);
        

    }


    render()
    {
        return(
            <div className='card postcard'>
                <div className='post-header'>
                 <Link to={`/profile/${this.props.post.author.username}`}> <img src={this.props.url+this.props.post.author.profileinfo.profile_pic} className='profilepic' />
                   </Link> <div className='post-author-info'>
                   <Link className='post-user-link' to={`/profile/${this.props.post.author.username}`}>
                        <span className='post-user'>{this.props.post.author.username}</span> </Link>
            <span className='post-location'>{this.props.post.location}</span>

            </div>
                </div>
                {/* <hr /> */}
                {this.props.post.pImage?<img src={this.props.url+this.props.post.pImage} className='post-img' alt='' />:<span></span>}
                <div className='post-caption'>
                <span className='post-user'>{this.props.post.author.username}: </span>  <pre>
                    {this.props.post.content}
                    </pre>

                </div>

               
                <i class="fa fa-thumbs-up" aria-hidden="true"></i>
            </div>
        )
    }
}

export default Post;