import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import { BrowserRouter } from 'react-router-dom';



class Sidebar extends Component
{

    constructor(props)
    {
        super(props);
    }
    
    render()
    {
        return(
           <div>
            <div className='sidebar'>
               
                
                <Link className='side-link '  to="/home">
                <div className='side-comp'>
                <div className='side-icon'>
                <i class="fa fa-home" aria-hidden="true"></i>
                </div>
                <div className='side-text'> Home </div>
                  
                </div>
                </Link>
                
              {this.props.user?<Link className='side-link'  to={`/profile/${this.props.user.username}`}>
                <div className='side-comp'>
                <div className='side-icon sd'>
                <i class="fa fa-user" aria-hidden="true"></i>
                </div>
                <div className='side-text'> Profile </div>
                  
                </div>
                </Link>:<div></div>}
                <Link className='side-link'  to="/messages">
                <div className='side-comp'>
                <div className='side-icon sd'>
                <i class="fa fa-envelope" aria-hidden="true"></i>
                </div>
                <div className='side-text'> Messages </div>
                  
                </div>
                </Link>
                <Link className='side-link'  to="/addpost">
                <div className='side-comp'>
                <div className='side-icon sd'>
                <i class="fas fa-plus-circle" aria-hidden="true"></i>
                </div>
                <div className='side-text'> Add Post </div>
                  
                </div>
                </Link>
                 
            </div>
            </div>
        )
    }
}

export default Sidebar;