import React,{ Component } from 'react';
import { Routes, Route} from 'react-router-dom';
import { BrowserRouter } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import SideBar from './SideBarComponent';
import Home from './HomeComponent';
import AddPost from './AddPostComponent';
import Profile from './ProfileComponent';




class Main extends Component
{

    constructor(props)
    {
        super(props);
       
    }

    render(){

      
        const profileview = ({match}) => {
            console.log(match.params.username);
            return (
                <Profile userprof = {match.params.username} token={this.props.token} url={this.props.url} />
            )
        }
 
      
       

       

        return(
            <div>
                 <BrowserRouter>
                 <div>
                
                <SideBar user={this.props.user} />
               
                
               <Routes>
        
        <Route   path="/profile/:username" component={profileview} />
        <Route exact path="/home" component={() => < Home token={this.props.token} url={this.props.url} /> } />
        {/* <Route exact path='/contactus' component={Contact} /> */}
        <Route  exact path="/addpost" component={() => < AddPost token={this.props.token} url={this.props.url} /> } />
        {/* <Redirect to='/home' /> */}
        
      </Routes>
      </div>
      </BrowserRouter>
            </div>
        )
    }
}


export default Main